<template>
  <div class="wrapper wrapper-content">
    <div class="ibox">
      <div class="ibox-content">
        <el-row :gutter="20">
          <el-form :model="registerForm" ref="registerForm" label-width="80px" size="small" :rules="registerRule">
            <el-col :span="24">
              <cd-select label="合作银行:" v-model="registerForm.coBankId" @selectChange="getBankName" prop="coBankId" :options="coBank" :offset="1"
                         :disabled="type != 1" label-width="90px"></cd-select>
              <cd-select label="分公司:" v-model="registerForm.launchOrgId" @selectChange="getOrgName" prop="launchOrgId" :options="orgList"
                         :disabled=" type != 1"></cd-select>
            </el-col>
            <el-col :sapn="24">
              <cd-select label="名单制类型:" label-width="90px" v-model="registerForm.overdueType" :disabled="type == 4 || type == 3" prop="overdueType" :options="listType" :offset="1"></cd-select>
              <cd-select label="风险类型:" v-model="registerForm.riskType" :disabled="type == 4 || type == 3" prop="riskType" :options="riskType"></cd-select>
            </el-col>
            <el-col :span="24">
              <cd-input label="客户姓名:" placeholder="请输入客户姓名" v-model="registerForm.customerName" prop="customerName" :disabled="true"
                        appendSlot="true" :offset="1">
                <el-button type="primary" size="small" @click="chooseCustomer" slot="appendSlot" :disabled="type != 1">
                  选择
                </el-button>
              </cd-input>
              <cd-select label="证件类型:" placeholder="请输入证件类型" v-model="registerForm.cardType" :disabled="true" :options="cardType"></cd-select>
              <cd-input label="证件号码:" placeholder="请输入证件号码" v-model="registerForm.cardNo" :disabled="true"></cd-input>
            </el-col>
            <el-form :model="detailForm" ref="detailForm" size="small" label-width="80px">
              <el-col :span="24">
                <cd-input label="贷款编号:" placeholder="请输入贷款编号" v-model="registerForm.projectNo" :disabled="true"
                          :offset="1"></cd-input>
                <cd-select label="贷款类型:" placeholder="请输入贷款类型" v-model="detailForm.loanType" :options="loanType"
                           :disabled="true"></cd-select>
                <cd-select label="期数:" placeholder="请输入期数" v-model="detailForm.loanTerm" :options="loanTerm" :disabled="true"></cd-select>
              </el-col>
              <el-col :span="24">
                <el-col :md="7" :xs="7" :sm="7" :lg="7" :offset="1">
                  <el-form-item label="放款时间:">
                    <el-date-picker placeholder="请输入放款时间" type="date" v-model="detailForm.bankPaymentDate"
                                    :disabled="true"   ></el-date-picker>
                  </el-form-item>
                </el-col>
                <!--<cd-input label="放款时间:" placeholder="请输入放款时间" v-model="detailForm.bankPaymentDate" :disabled="true"-->
                          <!--:offset="1"></cd-input>-->
                <cd-input label="放款金额:" placeholder="请输入放款金额" v-model="detailForm.bankPaymentAmount" :disabled="true"></cd-input>
                <cd-input label="贷款金额:" placeholder="请输入贷款金额" v-model="detailForm.loanAmount" :disabled="true"></cd-input>
              </el-col>
              <el-col :span="24">
                <cd-input label="业务组:" placeholder="请输入业务组" v-model="detailForm.userGroupName" :disabled="true"
                          :offset="1"></cd-input>
                <cd-input label="客户经理:" placeholder="请输入客户经理" v-model="detailForm.launchUserName" :disabled="true"></cd-input>
                <cd-input label="风险经理:" placeholder="请输入风险经理" v-model="detailForm.riskManagerName" :disabled="true"></cd-input>
              </el-col>
              <el-col :span="24">
                <cd-input label="营销品牌:" placeholder="请输入营销品牌" v-model="detailForm.carModelName" :disabled="true"
                          :offset="1"></cd-input>
                <cd-select label="汽车类型:" v-model="detailForm.carType" :disabled="true" :options="carType"></cd-select>
                <cd-input label="开票价:" placeholder="请输入开票价" v-model="detailForm.billingPrice" :disabled="true"></cd-input>
              </el-col>
              <el-col :span="24">
                <cd-input label="车牌号:" placeholder="请输入车牌号" v-model="detailForm.plateNo" :disabled="true"
                          :offset="1"></cd-input>
                <el-col :md="7" :xs="7" :sm="7" :lg="7">
                  <el-form-item label="抵押时间:">
                    <el-date-picker placeholder="请输入抵押时间" type="date" v-model="detailForm.mortgageTime"
                                    :disabled="true" value-format="yyyy-MM-dd"></el-date-picker>
                  </el-form-item>
                </el-col>
                <!--<cd-input label="抵押时间:" placeholder="请输入抵押时间" v-model="detailForm.mortgageTime" :disabled="true"></cd-input>-->
                <el-col :md="7" :xs="7" :sm="7" :lg="7">
                  <el-form-item label="是否拖车:">
                    <el-radio-group v-model="detailForm.isDragCar">
                      <el-radio :label="1" :disabled="true">是</el-radio>
                      <el-radio :label="0" :disabled="true">否</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
              </el-col>
              <el-col :span="24">
                <el-col :md="7" :xs="7" :sm="7" :lg="7" :offset="1">
                  <el-form-item label="是否垫款:">
                    <el-radio-group v-model="detailForm.isFee" :disabled="true">
                      <el-radio :label="1">是</el-radio>
                      <el-radio :label="0">否</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
                <el-col :md="7" :xs="7" :sm="7" :lg="7">
                  <el-form-item label="是否结清:">
                    <el-radio-group v-model="detailForm.isSettlement" :disabled="true">
                      <el-radio :label="1">是</el-radio>
                      <el-radio :label="0">否</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
                <el-col :md="7" :xs="7" :sm="7" :lg="7">
                  <el-form-item label="是否处置:">
                    <el-radio-group v-model="detailForm.isManger" :disabled="true">
                      <el-radio :label="1">是</el-radio>
                      <el-radio :label="0">否</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
              </el-col>
              <el-col :span="24">
                <cd-select label="诉讼类型:" placeholder="请输入诉讼类型" v-model="detailForm.lawsuitType" :disabled="true"
                          :offset="1" :options="lawsuitType"></cd-select>
                <el-col :md="7" :xs="7" :sm="7" :lg="7">
                  <el-form-item label="诉讼日期:">
                    <el-date-picker placeholder="请输入诉讼日期" type="date" v-model="detailForm.lawsuitDate"
                                    :disabled="true" value-format="yyyy-MM-dd"></el-date-picker>
                  </el-form-item>
                </el-col>
                <!--<cd-input label="诉讼日期:" placeholder="请输入诉讼日期" v-model="detailForm.lawsuitDate" :disabled="true"></cd-input>-->
                <cd-select label="处理结果:" v-model="registerForm.status" :disabled="true" v-if="type != 1 && type == 3"
                           :options="dealResult"></cd-select>
              </el-col>
            </el-form>
            <cd-input type="textarea" v-model="registerForm.justification" prop="justification" :rows="4" label="核查缘由:" :col-md="18" :col-xs="18" :col-sm="18" :col-lg="18"
                      :offset="1" :disabled="type == 3 || type == 4"></cd-input>
            <cd-input type="textarea" v-model="registerForm.companyFeedback" prop="companyFeedback" :rows="4" label="分公司反馈:" :col-md="18" :col-xs="18" :col-sm="18" :col-lg="18"
                      :offset="1" v-if="(type != 1 && registerForm.companyFeedback) || type == 4" :disabled="type !=4 "></cd-input>
            <el-col :span="21" style="text-align: right">
              <el-button type="primary" size="small" v-if="type == 4 && registerForm.status == 1" @click="dealFinish">处理完成</el-button>
              <el-button type="primary" size="small" @click="save" v-if="type != 3">保存</el-button>
              <el-button type="primary" size="small" @click="close">关闭</el-button>
            </el-col>
          </el-form>
        </el-row>
      </div>
      <el-row :gutter="20">
        <el-dialog title="客户选择" width="80%" :visible.sync="customerDialog" @open="openDialog">
          <el-row :gutter="20">
            <el-form :model="customerForm" ref="customerForm" label-width="80px" size="small">
              <el-col :span="24">
                <cd-input label="客户名称:" placeholder="请输入客户名称" v-model="customerForm.customerName" prop="customerName" :offset="6"></cd-input>
                <cd-input label="证件号码:" placeholder="请输入证件号码" v-model="customerForm.cardNo" prop="cardNo"></cd-input>
              </el-col>
              <el-col :span="24">
                <cd-input label="手机号码:" placeholder="请输入手机号码" v-model="customerForm.mobilePhone" prop="mobilePhone" :offset="6"></cd-input>
              </el-col>
              <cd-search-button @search="customerSearch" @reset="resetForm"></cd-search-button>
            </el-form>
            <cd-table
              :columns="columns"
              :params="customerParams"
              url="riskProfile/postLoanCustomerList"
            ></cd-table>
          </el-row>
        </el-dialog>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import cdInput from '../../../components/Input.vue';
  import cdSelect from '../../../components/Select.vue';
  import cdTable from '../../../components/Table.vue';
  import cdSearchButton from '../../../components/searchButton.vue';
  import {Fetch,getSystem,getAllBank,getArgs,getOrg,closeTab,tip,codeLibrary} from '../../../common/js/iframe';
  export default{
    /*
     type: 1: 风险档案登记 2: 修改 3: 查看详情 4: 反馈登记
     */
    name: '',
    components: {
      'cd-input': cdInput,
      'cd-select': cdSelect,
      'cd-table': cdTable,
      'cd-search-button': cdSearchButton
    },
    data(){
      return {
        type: 1,
        id: '',
        customerParams:{},
        customerForm: {
          customerName:'',
          mobilePhone:'',
          cardNo:''
        },
        customerDialog: false,
        tableParams: {},
        columns: [
          {label: '客户', prop: 'customerName'},
          {label: '身份证', prop: 'cardNo'},
          {label: '证件类型', prop: 'cardType',formatter: function (row,coumn,value) {
            return ['','身份证','军官证','侨胞证','外籍人士'][value];
          }},
          {label: '项目编号', prop: 'projectNo'},
          {label: '客户经理', prop: 'launchUserName'},
          {label: '手机号码', prop: 'mobilePhone'},
          {
            label: '操作', operations: [
            {label: '选择', func: this.addCustomer}
          ]
          }
        ],
        detailForm: {
          cardType:'',
          cardNo:'',
          loanTermString: '',
          loanTerm: '',
          loanType:'',
          bankPaymentDate:'',
          loanAmount: '',
          bankPaymentAmount: '',
          userGroupName: '',
          launchUserName: '',
          riskManagerName: '',
          carModelName: '',
          carType: '',
          billingPrice: '',
          plateNo: '',
          mortgageTime: '',
          isDragCar: '',
          isFee: '',
          isSettlement: '',
          isManger: '',
          lawsuitType: '',
          lawsuitDate: ''
        },
        registerForm: {
          cardType:'',
          cardNo:'',
          coBankId: '',
          coBankName: '',
          launchOrgId: '',
          launchOrgName: '',
          customerName: '',
          customerId:'',
          projectNo: '',
          projectId: '',
          status: '',
          justification: '',
          companyFeedback: '',
          overdueType: '',
          riskType: '',
        },
        registerRule: {
          coBankId: [{required: true,message:'合作银行必填'}],
          launchOrgId: [{required: true,message:'分公司必填'}],
          customerName: [{required: true,message:'客户姓名必填'}],
          justification: [{required: true,message:'核查缘由必填'}],
          companyFeedback: [{required: true,message:'反馈内容必填'}],
          riskType: [{required:true,message: '风险类型必填'}],
          overdueType: [{required:true,message: '名单制类型必填'}]
        },
        coBank: [],
        orgList: [],
        loanType: [
          {label:'车主贷',value: 5},
          {label:'车价贷',value: 6},
          {label:'易融贷',value: 7}
        ],
        cardType: [
          {label:'身份证',value: 1},
          {label:'军官证',value: 2},
          {label:'侨胞证',value: 3},
          {label:'外籍人士',value: 4},
        ],
        carType: [
          {label:'新车',value: 1},
          {label:'二手车',value: 2}
        ],
        dealResult: [
          {label: '处理中', value: 1},
          {label: '处理完成', value: 2}
        ],
        lawsuitType: [
          {label:'公司起诉',value: 1},
          {label:'银行起诉',value: 2}
        ],
        loanTerm: [
          {label: '12期',value: 1},
          {label: '18期',value: 2},
          {label: '24期',value: 3},
          {label: '36期',value:4}
        ],
        riskType: [],
        listType: []
      }
    },
    methods: {
      clearCustomer: function () {
        this.registerForm.customerName = '';
        this.registerForm.customerId = '';
        this.registerForm.cardType = '';
        this.registerForm.cardNo = '';
        this.registerForm.projectNo = '';
      },
      getBankName(val){
        this.clearCustomer();
        let obj = this.coBank.filter(function (item) {
          return item.value === val;
        });
        this.registerForm.coBankName = obj[0].label;
      },
      getOrgName(val){
        this.clearCustomer();
        let obj = this.orgList.filter(function (item) {
          return item.value === val;
        });
        this.registerForm.launchOrgName = obj[0].label;
      },
      //选择用户
      chooseCustomer: function () {
        let _this = this;
        if(this.registerForm.coBankId && this.registerForm.launchOrgId){
          this.customerDialog = true;
        }else {
          tip(this,{
            type:'warning',
            message: '请先选择合作银行和分公司'
          })
        }
      },
      openDialog(){
        let _this = this;
        setTimeout(function () {
          _this.customerParams = Object.assign(_this.customerForm,
            {
              coBankId: _this.registerForm.coBankId,
              launchOrgId: _this.registerForm.launchOrgId
            });
        },0);

      },
      customerSearch(){
        this.customerParams = Object.assign({}, this.customerForm,
          {
            coBankId: this.registerForm.coBankId,
            launchOrgId: this.registerForm.launchOrgId
          });
      },
      resetForm(){
        this.$refs['customerForm'].resetFields();
      },
      addCustomer(item, index){
        let _this = this;
        this.registerForm.customerName = item.customerName;
        this.registerForm.customerId = item.customerId;
        this.registerForm.cardType = item.cardType;
        this.registerForm.cardNo = item.cardNo;
        this.registerForm.projectNo = item.projectNo;
        this.registerForm.projectId = item.projectId;
        this.customerDialog = false;
        Object.keys(this.detailForm).map(function (item) {
          _this.detailForm[item] = '';
        });
        Fetch(this,'riskProfile/projectDetail',{id: this.registerForm.projectId}).then(function (res) {
          _this.detailForm = res.data;
        })
      },
      close(){
        closeTab();
      },
      save(){
        let _this = this;
        this.$refs['registerForm'].validate().then(function () {
          Fetch(_this, 'riskProfile/addRiskProfile',Object.assign({},_this.registerForm,_this.detailForm,{id: _this.id || ''})).then(function () {
            tip(_this,{
              message: '保存成功'
            });
            closeTab();
          }).catch(function () {
          });
        }).catch(function () {
        })
      },
      dealFinish(){
        let _this = this;
        this.$refs['registerForm'].validate().then(function () {
          Fetch(_this,'riskProfile/managerUpdate',{id: _this.id || '',status: _this.registerForm.status,companyFeedback: _this.registerForm.companyFeedback}).then(function () {
            tip(_this,{
              message: '处理成功'
            });
            closeTab();
          }).catch(function(){});
        });
      }
    },
    mounted(){
      let _this = this;
      getSystem(this);
      getAllBank(this, 'coBank');
      getOrg(this, 'orgList');
      this.type = getArgs()['type'];
      this.id = getArgs()['id'];
      if(this.id || getArgs()['projectId']){
        let url = getArgs()['projectId'] ? 'riskProfile/projectDetail' : 'riskProfile/detail';
        let id = getArgs()['projectId'] || this.id;
        Fetch(this,url,{id: id}).then(function (res) {
          let dataBase = res.data;
          Object.keys(_this.detailForm).map(function (item) {
            _this.detailForm[item] = dataBase[item];
          });
          Object.keys(_this.registerForm).map(function (item) {
            _this.registerForm[item] = dataBase[item];
          })
        })
      }
      codeLibrary(this,{riskType: 'riskType',overdueType: 'listType'})
    }
  }
</script>



